<style>
.fina_box{width: 100%; height: auto;}
.fina_ul{margin: 0; padding: 0; width: 100%;height: auto;}
.fina_li{height: 40px;width: 100%;line-height: 40px;font-size: 14px;color: #222;border-bottom: 1px dashed #ddd;  }
.fina_li label{cursor: pointer;}
.fina_p{color: #4C4B4B; font-size: 12px;margin-top: 10px;margin-bottom: 10px;}
.fina_lis{float: right;margin-right: 10px;}


</style>

<template>
<div class="fina_box">
	<h1>待处理</h1>
	<p class="fina_p">选中报表就表示已完成</p>
	 <ul class="fina_ul">
	 		<li class="fina_li" v-for="(item,index) in fina_data" @click="SetlocalStorage(index,true)" v-if="!item.checkboxs">
	 			<input type="checkbox" v-model="item.checkboxs" :id="index" /> <label    :for="index">{{item.title}}</label><span class="fina_lis">{{item.times}}</span></li>
	 </ul>
	 <h1>已处理</h1>
	<p class="fina_p">选中报表就表示未完成</p>
	 <ul class="fina_ul">
	 	  <li class="fina_li"  v-for="(item,index) in fina_data" @click="SetlocalStorage(index,false)"  v-if="item.checkboxs">
	 	  	<input type="checkbox" v-model="item.checkboxs" :id="index" /><label  :for="index">{{item.title}}</label><span class="fina_lis">{{item.times}}</span></li>
	 </ul>
</div>
</template>

<script>
	import setstorage from '../js/setLocalstorage.js'
	export default{
		data(){
			return{
				fina_data:[
					{times:"2019-05-13", checkboxs:false, title:"市中心召开办公会研究电子化巡查整改等工作"},
					{times:"2019-05-13", checkboxs:false, title:"竹山办事处： 召开住房公积金业务交流座谈会"},
					{times:"2019-05-13", checkboxs:false, title:"房县办事处：“ 上门服务” 打通服务房企最后一公里"},
					{times:"2019-05-13", checkboxs:false, title:"武当山办事处： 深入楼盘宣传 做好服务对接"},
					{times:"2019-05-10", checkboxs:false, title:"武当山办事处： 业务政策进机关 宣传服务落实处"},
					{times:"2019-05-09", checkboxs:false, title:"审计监察科： 集中学习抓思想 明确任务抓落实"},
					{times:"2019-05-07", checkboxs:false, title:"市公积金中心： 五措并举助推公积金服务提档升级"},
					{times:"2019-05-06", checkboxs:false, title:"武当山办事处启动“ 党风廉政宣教月” 和“ 住房公积金宣传月” 活动"},
					{times:"2019-05-06", checkboxs:false, title:"市公积金中心： 加大整治力度 形成常态震慑"},
					{times:"2019-04-30", checkboxs:false, title:"武当山办事处： 安排部署第二十个党风廉政宣教月活动"},
					{times:"2019-05-06", checkboxs:false, title:"市公积金中心：加大整治力度 形成常态震慑"},
	                {times:"2019-04-30", checkboxs:false, title:"武当山办事处：安排部署第二十个党风廉政宣教月活动"},
	                {times:"2019-04-30", checkboxs:false, title:"市公积金中心召开第二十个党风廉政宣教月动员大会"},
	                {times:"2019-04-30", checkboxs:false, title:"胡建明：用足用好公积金 助圆职工“安居梦”"},
	                {times:"2019-04-29", checkboxs:false, title:"中心机关及各分支机构积极举办4·23世界读书日座谈活动"},
	                {times:"2019-04-28", checkboxs:false, title:"我中心在全市职工演讲比赛中喜获佳绩"},
	                {times:"2019-04-28", checkboxs:false, title:"养成健康生活方式  提升幸福生活指数"},
	                {times:"2019-04-28", checkboxs:false, title:"防范金融风险  增强安全意识"},
	                {times:"2019-04-28", checkboxs:false, title:"喜报频传亮点多   奋勇争先创一流"},
	                {times:"2019-04-26", checkboxs:false, title:"房县办事处：加强“办法”学习，坚守保密防线"}
				],
				checkboxs:false,
				
			}
		},
		methods:{
			SetlocalStorage:function(index,bool){
				this.fina_data[index].checkboxs=bool
					//sessionStorage.setItem("item", JSON.stringify(this.fina_data))
					setstorage.set_item('item',this.fina_data)
				}
		},
		mounted(){
			let SetlocalStorage=setstorage.get_item('item')
			 if(SetlocalStorage){
			 	this.fina_data=SetlocalStorage
			 }
		}
	}
</script>




