import Vue from 'vue'
import Router from 'vue-router'
import homePage from '@/components/home_page'//首页
import NewsPage from '@/components/news_page'//新闻列表页
import Newslist from '@/components/news_list_con'//新闻详情页 
import NeweStlist from '@/components/newest_list'//最新动态
import Market from '@/components/market'//市场动态
import Business from '@/components/business'//业务范围
import Finance from '@/components/finance'//财务报告
 

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: homePage
    },{
      path: '/home',
      name: 'home',
      component: homePage
    },
    {
      path: '/news',
      name:'news',
      component: NewsPage,
    },
     {//这是用query 传递参数的路由配置  在用query传递参数的时候，不用接收参数，否则无法获取 
      path:"/news_list_con", 
      name: 'news_list_con',
      component: Newslist
    },
    /*{//这是在params 传递参数的情况下，需要接收参数，
      path:"/news/news_list_con:/id/:title",
      name: 'news_list_con',
      component: Newslist
    },*/
   {
   	path:'/newstlist',
   	name:'newstlist',
   	component:NeweStlist
   },
    {
   	path:'/market',
   	name:'market',
   	component:Market
   },
   {
   	path:'/business',
   	component:Business
   },
   {
   	path:'/fina',
   	name:'fina',
   	component:Finance
   }
  ]
})



