.home_next,.home_prev{align-items: center; display: flex;visibility:hidden ;justify-content: center;width: 50px;height: 80px;top:50%;background: rgba(153, 153, 153, 0.52);margin-top: -50px;position: absolute;cursor: pointer;z-index: 9999;}
.home_next{left: 30px;transform: rotateZ(180deg);}
.home_page_box:hover .home_next,.home_page_box:hover .home_prev{visibility: visible;}
.home_prev{right:30px;}
.home_carousel_makr{height: 10px;width: 300px;position: absolute;bottom: 10px;z-index: 8888; left: 50%;margin-left: -150px;}
.home_carousel_makr li{width: 45px;margin-right: 5px;margin-top: 4px; height: 2px;background: #C9C9C9;float: left;}
.makr_active{background: #007FFF !important;}

.home_cloud{width: 100%;height: auto;text-align: center;margin-top: 40px;color: #333333;}
.home_cloud h3{margin-top: 10px;}
.home_cloud_title{margin-bottom: 15px; text-align: center;}
.home_cloud_title h3{color: #8c8c8c; font-size: 14px;font-weight: inherit;}
.home_cloud_content{width: 100%;height: auto;margin-top: 70px;}
.home_cloud_content ul{position: relative;}
.home_cloud_content ul:after{content: ''; display: block;clear: both;}
.home_cloud_li {width: 300px;height: 400px;float: left;position: relative;}
.home_min_box {position: absolute;top: 0;opacity: 1; z-index: 10; width: 300px;height: 400px;border-right: 1px solid #efefef;position: relative;border-top: 1px solid #efefef; float: left; box-sizing: border-box;background: #fff;}
.cloud_active{width: 400px;height: 500px;}
.cloud_active .max_cloud_con{transition: 0.4s ease-out; z-index: 100; position: absolute;top: 0;left: 0;background: #fff;opacity: 1; width: 400px;box-shadow: #999 0px 0px 15px 1px;height: 500px;margin-top: -50px;z-index: 100; }
.min_cloud_con{margin: 20px 30px  0px; padding-bottom: 20px;border-bottom: 1px solid #ddd;padding: 10px 20px;text-align: left;}
.min_content{margin: 10px;border: none;}
.cloud_active .home_min_box{opacity: 0;}
.min_cloud_con h2{font-weight: inherit;text-align: left;}
.home_avenir{ margin-top:10px;text-align: left; font-size: 26px; font-family: serif;color: #8c8c8c;} 
.min_cloud_list{padding: 12px;height: 30px;font-size: 16px}
.min_cloud_list span{font-size: 18px;margin-right: 5px;}
.max_see{display: block;padding: 10px 30px;text-align: left;color: #8f8f8f;}
.max_cloud_title{padding: 30px; background: #2d2d2d;color: #fff;font-size: 16px;text-align: left;font-weight: initial;}
.max_cloud_title span{margin-left: 15px;color: #c7c7c7;}
.max_cloud_con{opacity: 0;}
.max_cloud_con , .max_cloud_list{width: 100%;height: 100%;}
.max_cloud_ul li{margin: 5px 30px;border-bottom: 1px solid #ddd;}
.max_cloud_ul li:nth-last-child(1){border: none;}
.max_cloud_list div{margin: 5px 0px;font-size: 16px; overflow: hidden;}
.max_cloud_list div p{float: left;margin-top: 5px;}
.max_cloud_list .iconfont{margin-right: 5px;float: left;font-size: 20px}
.max_cloud_list a{float: right; padding: 4px 12px;color: #949494;border: 1px solid #ddd;font-size: 14px}
.max_cloud_list>span{text-align: left;display: block;margin: 10px 0px;padding-left: 26px;clear: both;}
.max_cloud_list>p{text-align: left;display: block;margin: 10px 0px;padding-left: 26px;}

.server_content{width: 100%;height: auto;background: #2B2B2B;}
.server_con_box{width: auto;height: auto;overflow: hidden;}
.server_tabs_ul{width: 200px;padding-bottom:0;background: rgba(0, 0, 0, 0.75);float: left;}
.server_tabs_ul li{color: #fff;cursor: pointer;margin-top: -1px;}
.server_tabs_ul li:nth-last-child(1){border: none;}
.tabs_li_div{border-bottom: 1px solid #5a5a5a;transition: 0.3s ;  padding: 10px 10px;margin-left: 15px;margin-right: 15px; }
.tabs_nav{margin-bottom: 15px;font-size: 16px}
.tabs_subtext{color: #c5c5c5;}
.tabs_li_active{background: linear-gradient(270deg, #0B61FF 0, #00A1FF 97%); }
.tabs_li_active .tabs_li_div{border-bottom: 1px solid #007FFF; padding-left: 20px;transition: 0.3s ;}
.tabs_li_active .tabs_subtext{color: rgba(255, 255, 255, 0.74);}
.server_tabs_con{position: relative; float: left;width: calc(100% - 200px);height: 450px;display: block;}
.server_tabs_con li{width: 80%; height: 100%;visibility: hidden; padding: 20px;margin: 0 auto;position:absolute;top: 0;left: 50px;z-index: -1;}
.server_tabs_con .server_tabs_con_active{visibility: visible; z-index: 100 ;}
.tabs_con_title{margin-top: 30px;font-size: 30px;color: #fff;font-weight: inherit; margin-left: -50px; opacity: 0; font-family: "微软雅黑";margin-bottom: 15px;}
.server_tabs_con_active .tabs_con_title,.server_tabs_con_active .tabs_con_t_text{margin-left: 0px; transition: 0.5s; opacity: 1;}
.tabs_con_t_text{color: #b9b9b9;font-size: 14px;line-height: 25px; margin-left: -50px;width: 100%;}
.ahref{padding: 8px 25px;color: #fff;transition: 0.3s;border: 1px solid #b9b9b9;margin-top: 30px;display: inline-block;}
.ahref:hover{background: #03A9F4;transition: 0.3s;}
.tabs_con_row{margin-top: 30px;color: #fff;}
.tabs_con_pic img{width: 30%;margin-right: 20px;max-height: 60px;min-width: 100px;}
.tabs_con_product{margin-top: 15px;}
.tabs_con_product span{ display: inline-block;padding: 5px 15px;transition: 0.3s; color: #fff;font-size: 14px;margin-right: 10px; border: 1px solid #848484;}
.tabs_con_product span:hover{background: #0086B3;cursor: pointer;transition: 0.3s;}



 
</style>


