<style>
	.newest_center,
	.newest_box {
		width: 100%;
		height: auto;
	}
	
	.newest_center {
		margin-top: 30px;
	}
	
	.newest_hader {
		height: 100px;
		padding: 10px;
		border-radius: 5px;
		box-shadow: #ccc 0px 0px 10px 0px;
	}
	
	.hader_pic {
		width: 100px;
		margin-left: 5px;
		height: 100%;
		float: left;
		margin-right: 15px;
	}
	
	.hader_pic img {
		width: 100%;
	}
	
	.hader_two {
		width: calc(100% - 150px);
		text-align: center;
	}
	
	.hader_one {
		float: left;
		height: 70px;
		line-height: 75px;
		font-size:36px;
		color: #2B2B2B;
		width: 100%;
		cursor: pointer;
	}
	.hader_one:hover,.hader_list p:hover{color: #03A9F4;}
	.hader_list {
		width: 100%;
		line-height: 35px;
		color: #8C8C8C;
		cursor: pointer;
	}
	
	.hader_list p {
		display: inline-block;
		margin: 0 15px;
	}
	
	.hader_two,
	.hader_list {
		float: left;
	}
	
	.content_box {
		width: 100%;
		height: auto;
		margin-top: 20px;
		border: 1px solid #ddd;
		border-radius: 5px;
		box-sizing: border-box;
	}
	
	.content_list_title {
		height: 35px;
		border-bottom: 1px solid #ccc;
		line-height: 35px;
		padding: 0 10px;
		font-weight: bold;
		font-size: 16px
	}
	.content_list_title span{display: inline-block;}
	.content_tubiao{float: right;display: inline-block;}
	.content_tubiao i{display: inline-block;float: left;height: 25px; margin-right:8px;margin-top:5px;width: 20px; cursor: pointer;}
	.content_tubiao i img{width: 100%;}
	.content_box ul {
		padding: 15px;
		margin: 0;
		width: 100%;
		box-sizing: border-box;
		overflow: hidden;
	}
	
	.content_box li {
		list-style: none;
		display: block;
		
	}
	
	.tent_list {
		width: 100%;
		height: 90px;
		margin-top: 8px;
		border-bottom: #ccc dashed 1px;
	}
	
	.tent_list span {
		display: inline-block;
		width: 110px;
		height: 100%;
		float: left;
		margin-right: 10px;
	}
	
	.tent_rows {
		float: left;
		width: calc(100% - 120px);
	}
	
	.tent_list span img {
		width: 100%;
	}
	
	.grid{width: 290px;height: 250px;float: left;margin: 0 15px;box-shadow: #dedede 0px 0px 10px 1px;margin-bottom: 15px;transition: 0.5s;}
	.grid span{display: inline-block; width: 100%;height: 200px; }
	.grid span img{width: 100%;height: 100%;}
	.grid h3{font-size: 14px; color: #222222;padding: 0 15px;}
	.grid:hover{ box-shadow: #b7b7b7 0px 0px 10px 1px; transition: 0.5s;}
	.tent_title {
		height: 30px;
		line-height: 30px;
	}
	
	.tent_title h3 {
		display: inline-block;
		float: left;
		cursor: pointer;
	}
	.tent_title h3:hover{color: #03A9F4;}
	
	.tent_title p {
		float: right;
		width: auto;
		display: inline-block;
		margin-right: 15px;
	}
	
	.tent_text {
		font-size: 14px;
		line-height: 25px;
		color: #797979
	}
