<style>
	 .market_content{width: 100%; height: auto;}
	 .market_box{width: 100%;height: auto;border-top: 1px solid #fff;margin-bottom: 15px;}
	 .market_box h1{text-align: center;margin-top: 30px;}
	 .market_con{width: 100%;height: 345px;background: #2B2B2B;margin-top: 10px;}
	 .market_con:after{display: block; content: ''; clear: both;}
	 .market_left{float: left;width: 250px;height: 100%;background: #007FFF; color: #fff;}
	 .market_right{float: left; width: calc(100% - 250px);height: 100%;padding:0 10px; box-sizing: border-box;}

	 .market_left_nav{height: 86px;transition: all 0.2s linear; width: 100%;border-top: 1px solid #ccc;text-align: center;padding-top: 15px; cursor: pointer; box-sizing: border-box;}
	 .market_left_nav p{height: 35px; line-height: 35px;}
	 .market_left_nav:nth-child(1){border: none;}
	 .market_left_nav_active{background: #ff5722;transition: all 0.2s linear}
	 .market_aon{width: 100%;height: 35px;line-height: 35px;color: #03A9F4;}
	 .market_aon:before{display: block;margin-right: 10px; content: '';border:3px solid #03A9F4;width: 0px;border-left-width: 0px;height: 10px;margin-left: 15px;margin-top: 10px;float: left;}
	 .right_con_tab{width: 100%;height: 35px;color: #fff;}
	 .right_con_tab span{display: block; position: relative; color: #fff; float: left;width:33.33%; text-align: center;line-height: 35px;cursor: pointer;}
	 .con_tab:nth-last-child(1){border: none;}
	 .con_tab_active{color: #008000;}
	 .con_tab_active:after{display: block;content: "";border: 2px solid #F1A899; width: 0;}
	 .right_con_box{height:77%; color: #fff;}
	 .bons{height: 100%;display: none;transition: all 0.2s linear;text-align: center; line-height: 15; background: #313131; }
	 .con_active{display: block;transition: all 0.2s linear}
	 .tab_c_active{color: #ff5722 !important;position: relative;}
	 .tab_c_active:before{display: block; content: ""; position: absolute;left: 50%;  margin-left: -90px; bottom: 0; border: 1px solid #ff5722 ;width: 180px;}
	 .thrre_content{width: 100%;height: 400px;margin-bottom: 15px;color: #fff; display: flex;justify-content: space-between;}
	 .thrre_pis{ height: 100%;width:320px;background: #2D2D2D;padding:0 20px;box-sizing: border-box;}
	 .market_til{margin-top:30px; text-align: center;margin-bottom: 10px;}
	 .thrre_top {height: 150px;width: 100%;text-align: center; border-bottom: 1px solid #4c4c4c;}
	 .thrre_top img{margin-top: 40px;}
	 .thrre_top p{margin-top: 10px;}
	 .thrre_mian{padding:0 0 15px 0px;}
	 .thrre_mian li{height: auto;overflow: hidden; padding-bottom: 10px; min-height: 40px; border-bottom: 1px solid #545454;padding-top: 15px;}
	 .thrre_mian li:hover .thrre_hide{display: block;height: 35px;transition: 0.5s;margin-top: 15px; opacity: 1;}
	 .thrre_mian li:nth-last-child(1){border: none;}
	 .thrre_pimg{display:inline-block; width: 25px;float: left;}
	 .thrre_pimg img{width: 100%;}
	 .thrre_m{float: left;margin-left: 15px;font-size: 16px;width: 80%;cursor: pointer;}
	 .thrre_hide{font-size: 12px;color: #b1b1b1;margin-top: 0px; overflow: hidden;height: 0;transition: 0.5s;opacity: 0;}
</style>

