<template>
	 <div class="news_box">
	 	<div class="news_serche">
	 		<input type="text" name="news_serche" id="news_serche" value="请输入关键词" v-model="news_input" />
	 		<button id="ser_ch"  >搜索</button>
	 	</div>
	 	 
	 	<div class="news_content">
	 		<div class="news_con_title">{{titles[0]}}</div>
	 		<div class="news_con_box">
	 			<ul>
	 				<li class="news_con_list" :id="item.id"  v-for="(item,index) in news_fn">
	 					<span  @click="news_luyou(item.id,item.title)"  class="list_title">{{item.title}}</span><span class="list_text">{{item.add_time}}</span></li>
	 			</ul>
	 		</div>
	 	</div>
	 	<div class="news_content" v-if="vshow" >
	 		<div class="news_con_title">{{titles[1]}}</div>
	 		<div class="news_con_box">
	 			<ul  >
	 				<li class="news_con_list" :id="item.id" v-for="item in news2_fn">
	 					<!--<span class="list_title"><router-link  :to="'/news_list_con'" > {{item.title}} </router-link></span><span class="list_text">{{item.time}}</span>-->
	 					<span @click="news_luyou(item.id,item.title)"  class="list_title"> {{item.title}} </span><span class="list_text">{{item.ptime}}</span>
	 				
	 				</li>
	 			</ul>
	 		</div>
	 	</div>
	 </div>
	 
</template>

<script>
	import ajax from 'axios';
	
	export default{
		data(){
			return{
				 news_hy_Data:[ ],
				 news_zx_Data:[],
				 news_input:"",
				 searchData:false,
				 searchData2:false,
				 insm:null,
				 vshow:false,
				 titles:["最近新闻","十堰行业新闻"]
			}
		},
		created(){
			
			 let _this=this
			 ajax.get('http://www.liulongbin.top:3005/api/getnewslist')
			 .then(function(data){
			 	_this.news_hy_Data=data.data.message;
			 })
			 .catch(function(erro){
			 	console.log("数据返回失败");
			 });
			 
			 //数据二
			  ajax.get('http://jsonplaceholder.typicode.com/posts')
			 .then(function(data){
				 console.log(data)
			 	_this.news_zx_Data=data.data
			 })
			 .catch(function(erro){
			 	console.log("数据返回失败")
			 });
	
		},
		methods:{
			news_luyou:function(id,news_title){
				   //this.$router.push({name:'news_list_con', params:{id:id,title:news_title}});//如果用params 应用name
				    this.$router.push({ path:'/news_list_con', query:{id:id,title:news_title}}); //如果用query  就用path
			},
		
			
		},
		computed:{
			news_fn:function(){
				this.news_hy_Data.reverse();
				var setData=[];
				
				for(var i=0; i<this.news_hy_Data.length;i++){
						if(this.news_hy_Data[i].title.search(this.news_input) !=-1){
						setData.push(this.news_hy_Data[i])
								this.searchData=false;
						}else{
								this.searchData=true;
					} 
				 }
				return setData
			},
			news2_fn:function(){
				this.news_zx_Data.reverse()
			  
				var setDatas=[];
				for(var i=0; i<this.news_zx_Data.length;i++){
					if(this.news_zx_Data[i].title.search(this.news_input) !=-1){
						
							setDatas.push(this.news_zx_Data[i])
						this.searchData2=false;
					}else{
						this.searchData2=true;
					}
				}
				return setDatas
			} 
		}
	}
</script>

<style>
	.news_box{width: 1000px;height: auto; margin-top: 15px; }
	.news_con_title{height: 35px;width: 100%;line-height: 35px; font-weight: bold;color: #2B2B2B; position: relative; padding: 0 20px; border-bottom: 1px solid #E6E6E6; box-sizing: border-box;font-size: 16px; font-family: "yaHei";}
	.news_con_title:after{display: block; content: ""; border: 4px #03a9f4 solid; width: 0px; position: absolute; top: 11px;height: 6px; left: 10px;border-left: #fff;}
	.news_con_box{width: 100%;height: auto; padding: 0 5px }
	.news_con_list{height: 35px;line-height: 35px; font-size: 16px; border-bottom: 1px dashed #e6e6e6;position: relative;}
	.news_con_list:after{display: block;content: "";border-radius: 50%; border: 2px solid #c7c7c7; position: absolute;left: 0; width: 0;top: 16px;}
	.news_con_list:nth-last-child(1){border-bottom: none;}
	.list_title{cursor: pointer;margin-left: 15px;} 
	.list_title:hover{color: #03A9F4;} 
	.list_text{float: right;margin-right: 20px;} 
	.news_content{margin-bottom: 15px; border: 1px solid #efefef; padding: 0px 15px; box-shadow: #ccc 0px 0px 10px 1px;}
	.news_serche{height:40px;}
	.news_serche input{height: 30px;border-radius: 5px; padding: 0 5px;border: 1px solid #ccc; width: 300px;}
	.news_serche button{border: 1px solid #ccc; height: 30px; width:60px;background: #03A9F4;color: #fff;cursor: pointer; margin-left: 30px; outline: none;}
</style>


