<style>
	.news_list_content{width: 100%;height: auto;border: 1px solid #ddd;margin-top: 5px; box-shadow: #ddd 0px 0px 10px 1px;margin-bottom: 20px;}
	.news_list_hader{height: 60px;width: 100%;margin-top: 30px; text-align: center;}
	.news_list_hader h2{margin-bottom: 15px;display: inline-block;width: auto; position: relative;}
	.news_list_can{width: 300px;margin: 0 auto;height: auto;color: #777;}
