	.news_list_txt {width: 150px;float: left; }
	.news_list_txt span{display: inline-block; width: auto;}
	.news_list_txt  p{display: inline-block;}
	.news_mian_con{font-size: 16px;line-height: 35px;padding: 25px;color: #666;}
	
	.news_mian_footer{height: 50px;padding: 0 20px;}
	.news_footer{height: 100%;line-height: 50px; width: 40%;}
